import React from 'react';
import ReactDOM from 'react-dom';
import './index.scss';
import App from './App';

declare let window: any;

const startApp = () => {
  ReactDOM.render(
    <React.StrictMode>
      <App />
    </React.StrictMode>,
    document.getElementById('root')
  );
}


if (window.cordova) {
  document.addEventListener('deviceready', startApp, false);
} else {
  startApp();
}


